@import "variables.scss";
@import "fonts.scss";

html,
body {
	font-family: "Lato Regular";
	height: 100%;
	margin: 0;
	width: 100%;
}

*:focus {
	outline: none;
}

#main {
	box-sizing: border-box;
	display: flex;
	height: 100%;
	padding: 1em;
	width: 100%;

	div {
		display: flex;
		flex-direction: column;

		p {
			color: black;
			font-family: "Lato Light";
			margin: 0;

			&#title {
				font-size: 2em;
				font-weight: bold;
			}

			&#name {
				font-size: 1.4em;
			}
		}

		div {
			padding: 1em;

			img {
				max-width: 100%;
			}
		}
	}

	canvas {
		border-radius: 0.4em;
		cursor: pointer;
	}
}
